@keyframes title-animation-sm {
    0% {
        opacity: 0;
        width: 0px;
    }
    50%{
        opacity: 0.75;
    }
    100% {
        opacity: 1;
        width: 140px;
    }
}

@keyframes title-animation-sm-back {
    0% {
        opacity: 1;
        width: 140px;
    }
    50% {
        opacity: 0.3;
    }
    80% {
        opacity: 0;
        width: 0px;
    }
    100% {
        opacity: 0;
        width: 0px;
    }
}

